import { ref } from 'vue';
export default {
  name: 'GoodsListview',
  props: ['selectedSidebarIndex'],

  setup() {
    const categorySelectedIndex = ref(0);
    const categoryList = [{
      title: '满减包邮',
      type: 0
    }, {
      title: '三级类目',
      type: 1
    }, {
      title: '三级类目',
      type: 2
    }];

    const categoryTapEvent = index => {
      categorySelectedIndex.value = index;
    };

    return {
      categoryList,
      categorySelectedIndex,
      categoryTapEvent
    };
  }

};